<template>
  <!--法律工具/赔偿计算-->
  <div id="pcjs">
    <h2 class="text-center lh-lg">2022年国家赔偿计算方式</h2>
    <p class="text-start lh-lg">
      最高人民法院公布自2021年5月20日起作出的国家赔偿决定涉及侵犯公民人身自由权的赔偿金标准为每日373.10元。
    </p>
    <p class="text-start lh-lg fw-bold">1、侵犯公民人身自由的，赔偿金按照下列规定计算：</p>
    <p class="text-start lh-lg">侵犯公民人身自由的天数（/天）*国家上年度职工日平均工资（/元）</p>
    <p class="text-start lh-lg">
      相关法条参考：<br>
      《中华人民共和国国家赔偿法》<br>
      第三十三条 侵犯公民人身自由的，每日赔偿金按照国家上年度职工日平均工资计算。
    </p>
    <p class="text-start lh-lg fw-bold">
      2、侵犯公民生命健康权的，赔偿金按照下列规定计算：<br>
      （1）造成身体伤害的：
    </p>
    <p class="text-start lh-lg">
      医疗费：实际医疗支出（/元）（须有医疗凭证：病历、收据等）<br>
      误工费：天数（/天）*国家上年度职工日平均工资（/元）<br>
      护理费：护理人数（/人）*护理天数（/天）*工费（/元）<br>
      注：<br>
      ①误工费最高额为国家上年度职工年平均工资的五倍
    </p>
    <p class="text-start lh-lg">
      <b>（2）造成部分或者全部丧失劳动能力：</b><br>
      医疗费：实际医疗支出（/元）（须有医疗凭证：病历、收据等） <br>
      误工费：天数（/天）*国家上年度职工日平均工资（/元） <br>
      护理费：护理人数（/人）*护理天数（/天）*工费（/元） <br>
      残疾赔偿金：按照伤残等级进行赔偿（/元） <br>
      残疾生活辅助器具费：实际购买支出（/元）（须有收据等材料） <br>
      康复费：后续恢复治疗费用（/元）（须有收据的等材料） <br>
      生活费：国家上年度职工年平均工资（/元）*抚养年限（/年）*人数（/人） <br>
      <b>注：</b> <br>
      ①误工费最高额为国家上年度职工年平均工资的五倍 <br>
      ②残疾赔偿金最高不超过国家上年度职工年平均工资的二十倍。 <br>
      ③生活费抚养年限的计算规则：被扶养人为未成年人的，计算至十八周岁；被扶养人无劳动能力又无其他生活来源的，计算二十年。但六十周岁以上的，年龄每增加一岁减少一年；七十五周岁以上的，按五年计算。
    </p>
    <p class="text-start lh-lg">
      相关法条参考：<br>
      <b>1.《中华人民共和国国家赔偿法》</b><br>
      <b>第三十四条</b>&emsp;造成身体伤害的，应当支付医疗费、护理费，以及赔偿因误工减少的收入。减少的收入每日的赔偿金按照国家上年度职工日平均工资计算，最高额为国家上年度职工年平均工资的五倍；<br>
      &emsp;&emsp;造成部分或者全部丧失劳动能力的，应当支付医疗费、护理费、残疾生活辅助具费、康复费等因残疾而增加的必要支出和继续治疗所必需的费用，以及残疾赔偿金。残疾赔偿金根据丧失劳动能力的程度，按照国家规定的伤残等级确定，最高不超过国家上年度职工年平均工资的二十倍。造成全部丧失劳动能力的，对其扶养的无劳动能力的人，还应当支付生活费；<br>
      &emsp;&emsp;造成死亡的，应当支付死亡赔偿金、丧葬费，总额为国家上年度职工年平均工资的二十倍。对死者生前扶养的无劳动能力的人，还应当支付生活费。<br>
      &emsp;&emsp;前款第二项、第三项规定的生活费的发放标准，参照当地最低生活保障标准执行。被扶养的人是未成年人的，生活费给付至十八周岁止；其他无劳动能力的人，生活费给付至死亡时止。<br>
      <b>2.《最高人民法院关于审理人身损害赔偿案件适用法律若干问题的解释》</b><br>
      <b>第六条</b>&emsp;医疗费根据医疗机构出具的医药费、住院费等收款凭证，结合病历和诊断证明等相关证据确定。赔偿义务人对治疗的必要性和合理性有异议的，应当承担相应的举证责任。<br>
      &emsp;&emsp;医疗费的赔偿数额，按照一审法庭辩论终结前实际发生的数额确定。器官功能恢复训练所必要的康复费、适当的整容费以及其他后续治疗费，赔偿权利人可以待实际发生后另行起诉。但根据医疗证明或者鉴定结论确定必然发生的费用，可以与已经发生的医疗费一并予以赔偿。<br>
      <b>第七条</b>&emsp;误工费根据受害人的误工时间和收入状况确定。<br>
      &emsp;&emsp;误工时间根据受害人接受治疗的医疗机构出具的证明确定。受害人因伤致残持续误工的，误工时间可以计算至定残日前一天。<br>
      &emsp;&emsp;受害人有固定收入的，误工费按照实际减少的收入计算。受害人无固定收入的，按照其最近三年的平均收入计算；受害人不能举证证明其最近三年的平均收入状况的，可以参照受诉法院所在地相同或者相近行业上一年度职工的平均工资计算。<br>
      <b>第八条</b>&emsp;护理费根据护理人员的收入状况和护理人数、护理期限确定。<br>
      &emsp;&emsp;护理人员有收入的，参照误工费的规定计算；护理人员没有收入或者雇佣护工的，参照当地护工从事同等级别护理的劳务报酬标准计算。护理人员原则上为一人，但医疗机构或者鉴定机构有明确意见的，可以参照确定护理人员人数。<br>
      &emsp;&emsp;护理期限应计算至受害人恢复生活自理能力时止。受害人因残疾不能恢复生活自理能力的，可以根据其年龄、健康状况等因素确定合理的护理期限，但最长不超过二十年。<br>
      &emsp;&emsp;受害人定残后的护理，应当根据其护理依赖程度并结合配制残疾辅助器具的情况确定护理级别。<br>
      <b>第十二条</b>&emsp;残疾赔偿金根据受害人丧失劳动能力程度或者伤残等级，按照受诉法院所在地上一年度城镇居民人均可支配收入标准，自定残之日起按二十年计算。但六十周岁以上的，年龄每增加一岁减少一年；七十五周岁以上的，按五年计算。<br>
      &emsp;&emsp;受害人因伤致残但实际收入没有减少，或者伤残等级较轻但造成职业妨害严重影响其劳动就业的，可以对残疾赔偿金作相应调整。<br>
      <b>第十三条</b>&emsp;残疾辅助器具费按照普通适用器具的合理费用标准计算。伤情有特殊需要的，可以参照辅助器具配制机构的意见确定相应的合理费用标准。<br>
      &emsp;&emsp;辅助器具的更换周期和赔偿期限参照配制机构的意见确定。<br>
      <b>第十四条</b>&emsp;丧葬费按照受诉法院所在地上一年度职工月平均工资标准，以六个月总额计算。<br>
      <b>第十五条</b>&emsp;死亡赔偿金按照受诉法院所在地上一年度城镇居民人均可支配收入标准，按二十年计算。但六十周岁以上的，年龄每增加一岁减少一年；七十五周岁以上的，按五年计算。<br>
      <b>第十六条</b>&emsp;被扶养人生活费计入残疾赔偿金或者死亡赔偿金。<br>
      <b>第十七条</b>&emsp;被扶养人生活费根据扶养人丧失劳动能力程度，按照受诉法院所在地上一年度城镇居民人均消费支出标准计算。被扶养人为未成年人的，计算至十八周岁；被扶养人无劳动能力又无其他生活来源的，计算二十年。但六十周岁以上的，年龄每增加一岁减少一年；七十五周岁以上的，按五年计算。<br>
      &emsp;&emsp;被扶养人是指受害人依法应当承担扶养义务的未成年人或者丧失劳动能力又无其他生活来源的成年近亲属。被扶养人还有其他扶养人的，赔偿义务人只赔偿受害人依法应当负担的部分。被扶养人有数人的，年赔偿总额累计不超过上一年度城镇居民人均消费支出额。
    </p>
    <p class="text-start lh-lg">
      <b>3、致人精神损害的：</b><br>
      （1）应当在侵权行为影响的范围内，为受害人消除影响，恢复名誉，赔礼道歉 <br>
      （2）造成严重后果的，应当支付相应的精神损害抚慰金。 <br>
      <b>注：</b><br>
      精神损害的赔偿数额根据以下因素确定：<br>
      &emsp;&emsp;（一）侵权人的过错程度，但是法律另有规定的除外；<br>
      &emsp;&emsp;（二）侵权行为的目的、方式、场合等具体情节；<br>
      &emsp;&emsp;（三）侵权行为所造成的后果；<br>
      &emsp;&emsp;（四）侵权人的获利情况；<br>
      &emsp;&emsp;（五）侵权人承担责任的经济能力；<br>
      &emsp;&emsp;（六）受理诉讼法院所在地的平均生活水平。
    </p>
    <p class="text-start lh-lg">
      <b>4、侵犯公民、法人和其他组织的财产权造成损害的，按照下列规定处理：</b><br>
      （1）处罚款、罚金、追缴、没收财产或者违法征收、征用财产的，返还财产 <br>
      （2）查封、扣押、冻结财产的，解除对财产的查封、扣押、冻结，造成财产损坏或者灭失的，依照第三项、第四项的规定赔偿 <br>
      （3）应当返还的财产损坏的，能够恢复原状的恢复原状，不能恢复原状的，按照损害程度给付相应的赔偿金 <br>
      （4）应当返还的财产灭失的，给付相应的赔偿金 <br>
      （5）财产已经拍卖或者变卖的，给付拍卖或者变卖所得的价款；变卖的价款明显低于财产价值的，应当支付相应的赔偿金 <br>
      （6）吊销许可证和执照、责令停产停业的，赔偿停产停业期间必要的经常性费用开支 <br>
      （7）返还执行的罚款或者罚金、追缴或者没收的金钱，解除冻结的存款或者汇款的，应当支付银行同期存款利息 <br>
      （8）对财产权造成其他损害的，按照直接损失给予赔偿。
    </p>
    <p class="text-start lh-lg">
      相关法条参考：<br>
      <b>《中华人民共和国国家赔偿法》</b><br>
      <b>第三十六条</b>&emsp;侵犯公民、法人和其他组织的财产权造成损害的，按照下列规定处理：<br>
      &emsp;&emsp;（一）处罚款、罚金、追缴、没收财产或者违法征收、征用财产的，返还财产； <br>
      &emsp;&emsp;（二）查封、扣押、冻结财产的，解除对财产的查封、扣押、冻结，造成财产损坏或者灭失的，依照本条第三项、第四项的规定赔偿；
      <br>
      &emsp;&emsp;（三）应当返还的财产损坏的，能够恢复原状的恢复原状，不能恢复原状的，按照损害程度给付相应的赔偿金； <br>
      &emsp;&emsp;（四）应当返还的财产灭失的，给付相应的赔偿金； <br>
      &emsp;&emsp;（五）财产已经拍卖或者变卖的，给付拍卖或者变卖所得的价款；变卖的价款明显低于财产价值的，应当支付相应的赔偿金；
      <br>
      &emsp;&emsp;（六）吊销许可证和执照、责令停产停业的，赔偿停产停业期间必要的经常性费用开支； <br>
      &emsp;&emsp;（七）返还执行的罚款或者罚金、追缴或者没收的金钱，解除冻结的存款或者汇款的，应当支付银行同期存款利息； <br>
      &emsp;&emsp;（八）对财产权造成其他损害的，按照直接损失给予赔偿。
    </p><br><br>
